<template>
<div id="windows">
    <el-button type="success" size="small" class="add" @click="add">添加窗口</el-button>
    <el-table :data="data" border style="width:70.1%" v-loading="loading">
        <!-- 窗口号 -->
        <el-table-column prop="windowNo" label="窗口号" width="180"></el-table-column> 
        <!-- 工作人员姓名 -->
        <el-table-column prop="staffName" label="工作人员姓名" width="180"></el-table-column>
        <!-- 窗口状态 -->
        <el-table-column prop="statusName" label="窗口状态" width="180"></el-table-column>
        <!-- 已完成 -->
        <el-table-column prop="windowStatusName" label="窗口当前工作状态" width="180"></el-table-column>
        <el-table-column prop="businessNowNum" label="已完成业务量" width="180"></el-table-column>
        <!-- 最大处理 -->
        <el-table-column prop="businessNum" label="最大办理业务量" width="180"></el-table-column>
        <!-- 操作 -->
        <el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
                <el-button type="text" @click="modifyMes(scope.row)" size="small">修改</el-button>
                <el-button type="text" @click="deleteWindows(scope.row)" size="small">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-dialog :visible.sync="modify" :title="dialogname"  append-to-body width="40%">
        <el-form :model="windowsMes" ref="windowsMes" label-width="20%" :rules="rule">
            <el-form-item label="窗口号" prop="windowNo">
                <el-input v-model="windowsMes.windowNo"></el-input>
            </el-form-item>
            <el-form-item label="工作人员姓名" prop="staffName">
                <el-select v-model="windowsMes.staffId" placeholder="安排工作人员" style="position:absolute;left:97px;">
                    <el-option v-for="work in worker" :key="work.value" :label="work.label" :value="work.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="业务类型" prop="businessId">
                <el-select v-model="windowsMes.businessId" placeholder="请选择业务类型" style="position:absolute;left:97px;">
                    <el-option v-for="item in business" :value="item.id" :label="item.businessName" :key="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="已完成业务量" prop="businessNowNum" v-if="type==0">
                <el-input v-model="windowsMes.businessNowNum" disabled></el-input>
            </el-form-item>
            <el-form-item label="最大办理业务量" prop="businessNum">
                <el-input v-model.number="windowsMes.businessNum"></el-input>
            </el-form-item>
            <el-form-item label="窗口状态" prop="status" id="switch">
                <el-switch style="position:absolute;top:10px;left:97px;" v-model="windowsMes.status" active-color="#13ce66" inactive-color="#ff4949" active-text="打开" inactive-text="关闭"></el-switch>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="modify=false" >取消</el-button>
            <el-button type="primary" @click="commit">确认</el-button>
        </div>
    </el-dialog>
</div>
</template>
<script>
export default {
    data(){
        return{
            modify:false,
            dialogname: "修改窗口信息",
            type:'0',  //0代表是修改  1代表是添加
            data:[],
            loading:true,
            worker:[], //员工列表
            business:[],
            windowsMes:{
                businessNo:'',
                staffName:'',
                statusName:'',
                businessNowNum:'',
                businessNum:'',
                businessId:'',
                staffId:''
            },
            rule:{
                windowNo:[{required:true}],
                staffId:[{required:true,message:""}],
                status:[{required:true}],
                businessNum:[{required:true,type:'number'}],
                businessId:[{required:true}]
            }
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        init(){
            this.axios.getWindows({}).then((res)=>{
                if(res.success){
                    this.loading = false
                    this.data = res.dataList
                }else{
                    this.$message.error('查询失败,请检查网络设置')
                }
            })
            this.axios.getWorkList({}).then((res)=>{
                if(res.success){
                    this.worker =[]
                    for(let i in res.dataList){
                        let data = {}
                        for(let key in res.dataList[i]){
                            if(key == 'id'){
                                data.value = res.dataList[i].id
                            }
                            if(key == 'staffName'){
                                data.label = res.dataList[i].staffName
                            }
                        }
                        this.worker.push(data)
                    }
                }else{
                    this.$message.error('获取工作人员列表错误')
                }
            })
            this.axios.getBusiness({}).then((res)=>{
                if(res.success)
                    this.business = res.dataList
            })
        },
        commit(){
            this.$refs["windowsMes"].validate((valid)=>{
                if(valid){
                    if(this.type == '0'){
                        if(this.windowsMes.status == true){
                            this.windowsMes.status = 1
                            this.windowsMes.statusName = '开放'
                        }
                        else{
                            this.windowsMes.status = 0
                            this.windowsMes.statusName = '关闭'
                        }
                        this.axios.modifyWindows(this.windowsMes).then((res)=>{
                            if(res.success){
                                this.modify = false
                                this.init()
                            }else{
                                this.$message.error('修改窗口信息失败')
                            }
                        })
                    }
                    if(this.type == '1'){
                        this.axios.addWindows(this.windowsMes).then((res)=>{
                            if(res.success){
                                this.modify = false
                                this.$refs['windowsMes'].resetFields()
                                this.$message.success('添加窗口成功')
                                this.init()
                            }
                        })
                    }
                }else
                    this.$message.error('表单有误')
            })
        },
        add(){
            this.dialogname = '添加窗口信息'
            this.modify = true
            this.type = '1'
            this.windowsMes ={}
        },
        modifyMes(row){
            this.modify=true
            this.type='0'
            this.dialogname = '修改窗口信息'
            this.axios.getWindows({'id':row.id}).then((res)=>{
                this.windowsMes = res.dataList[0]
                if(this.windowsMes.status == 1){
                    this.windowsMes.status = true
                }
                if(this.windowsMes.status == 0){
                    this.windowsMes.status = false
                }
            })
        },
        deleteWindows(row){
            this.axios.deleteWindows({id:row.id}).then((res)=>{
                if(res.success){
                    this.$message.success("删除成功")
                    this.init()
                }
            })
        }
    }
}
</script>
<style lang="scss">
#windows{
    .add{
        position: relative;
        top: 100px;
        left: 365px;
    }
    .el-table{
        border:1px solid #000;
        border-radius: 7px 7px 7px 7px;
        box-shadow:0px 0px 3px #000;
        position: relative;
        top:100px;
        left: 150px;
    }
    .el-form{
        .el-form-item{
           .el-form-item__label{
               margin-left: 90px !important;
           }
        }
        .el-form-item__content{
            margin-right: 20%;
        }
    }
}
</style>


